header {
  text-align: center;
}

header img {
  height: 6em;
  width: 6em;
  padding: 1.5em;
  will-change: filter;
  transition: filter 300ms;
}

header img:hover {
  animation-play-state: paused;
  filter: drop-shadow(0 0 2em #61dafb);
}

main {
  text-align: center;
}

main h2 {
  text-align: left;
  text-decoration: underline;
  animation: color-change infinite 10s;
}

.text:nth-child(1) {
  animation: color-change 7s infinite;
}
.text:nth-child(2) {
  animation: color-change 7s infinite 0.25s;
}
.text:nth-child(3) {
  animation: color-change 7s infinite 0.5s;
}

main p {
  text-align: left;
}

main ul {
  display: contents; 
}

main li {
  text-align: left;
}

footer {
  text-align: center;
}

footer a {
  padding: 1rem;
}

footer img {
  width: 32px;
  height: 32px;
  transition: transform 0.3s ease-in-out;
}

footer img:hover {
  transform: scale(1.2);
}

footer p {
  text-align: center;
  animation: color-change 60s infinite;
}

@keyframes logo-spin {
  from {
      transform: rotate3d(rotateX(0deg) rotateY(0deg) rotateZ(0deg));
  }

  to {
      transform: rotateX(360deg) rotateY(360deg) rotateZ(360deg);
  }
}

@media (prefers-reduced-motion: no-preference) {
  header img {
      animation: logo-spin infinite 10s linear;
      transform-style: preserve-3d;
  }
}

@media (prefers-color-scheme: light) {
  main {
    color: #1D9BF0;
  }
  strong {
    color: #000;
  }
  b {
    color: #39FF14;
  }
  b:hover {
    color: #9B00D9;
  }
  p:hover,
  li:hover {
    color: #FF073A
  }
}

@media (prefers-color-scheme: dark) {
  main {
    color: #00e5ff;
  }
  strong {
    color: #000;
  }
  b {
    color: #ffffff;
  }
  b:hover {
    color: #39FF14;
  }
  p:hover,
  li:hover {
    color: #e0f64f
  }
}

@keyframes color-change {
  0% {
    color: #39FF14; /* Neon Green */
  }
  10% {
    color: #FF10F0; /* Neon Pink */
  }
  20% {
    color: #1D9BF0; /* Neon Blue */
  }
  30% {
    color: #9B00D9; /* Neon Purple */
  }
  40% {
    color: #FFFF00; /* Neon Yellow */
  }
  50% {
    color: #FF073A; /* Neon Red */
  }
  60% {
    color: #FF5F00; /* Neon Orange */
  }
  70% {
    color: #00FFFF; /* Neon Cyan */
  }
  80% {
    color: #9400D3; /* Neon Violet */
  }
  90% {
    color: #FF3B77; /* Neon Coral */
  }
  100% {
    color: #39FF14; /* Trở lại với Neon Green */
  }
}